.coffee-menu {
  margin: 5px auto 90px;
  min-height: 70vh;
}
.coffee-menu .category {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 65px;
  left: 0;
  margin: 10px auto 25px;
  background-color: white;
  width: 100%;
  z-index: 2;
}
.coffee-menu .category .arrow_btn {
  display: flex;
  margin-inline: 6px;
  cursor: pointer;
}
.coffee-menu .category .arrow_btn svg {
  width: 36px;
  height: 36px;
  padding: 5px;
  background-color: var(--secondry-color);
  border-radius: 50%;
  color: var(--primery-color);
  transition: 0.3s;
}
.coffee-menu .category .arrow_btn svg:hover {
  color: var(--secondry-color);
  background-color: var(--primery-color);
}
.coffee-menu .category .arrow_btn:nth-of-type(1) svg {
  transform: rotateY(180deg);
}
.coffee-menu .category .category-content {
  margin-inline: auto;
  display: flex;
  align-items: center;
  background-color: #fcfcfc;
  padding: 10px;
  overflow-x: auto;
  scroll-behavior: smooth;
}
.coffee-menu .category .category-content button {
  white-space: nowrap;
  flex-grow: 1;
  margin: 7px;
  padding: 10px 25px;
  background: var(--secondry-color);
  color: #7e4226;
  border: 0;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  text-transform: capitalize;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  transition: 0.5s ease-out;
}
.coffee-menu .category .category-content button:hover {
  color: white;
  background: #7e4226;
}
.coffee-menu .category .category-content button.active {
  color: white;
  background: #7e4226;
}
.coffee-menu .category .category-content::-webkit-scrollbar {
  height: 5px;
  display: none;
}
.coffee-menu .box-content {
  margin-top: 100px;
  width: 95%;
  margin-inline: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  gap: 0 16px;
}
@media screen and (max-width: 1325px) {
  .coffee-menu .box-content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 701px) {
  .coffee-menu .box-content {
    grid-template-columns: repeat(1, 1fr);
  }
}/*# sourceMappingURL=CoffeMenu.css.map */