.coffee-box {
  position: relative;
  width: 100%;
  margin-inline:auto ;
  display: flex;
  border-radius: 7px;
  box-shadow: 0px 2px 7px 2px #ccc;
  background: white;
  opacity: 0.5;
  transition:  0.3s ease-in-out;
  padding: 8px;
  padding-left: 0;
  margin-top: 24px;
}

.coffee-box.fade-in {
  top: 0;
  opacity: 1;

}

.coffee-box .img-content {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 200px;
  aspect-ratio: 4/4;
  padding: 10px 24px;  
  background: #fae8c3;
  border-radius: 6px;
}

.coffee-box .img-content img {
  display: inline-block;
  width: 100%;
  z-index: 0;
  background: none;
  transform: scale(1.5);
  object-fit: contain;
  filter: drop-shadow(2px 4px 1px #ccc);
}
.coffee-box #no-out-img {
  width: 200px;
  aspect-ratio: 4/4;
  background: #fae8c3;
  border-radius: 6px;
}
.coffee-box #no-out-img img{
  transform: scale(1.5);
  right: 0;
  top: 15px;
}

.coffee-box .coffee-info {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding: 7px 10px;
}

.coffee-box .coffee-info .title .head {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.coffee-box .coffee-info .title h2 {
  font-weight: 700;
  font-size: 20px;
  color: var(--primery-color);
}

.coffee-box .coffee-info .title button {
  all: unset;
  font-weight: 600;
  font-size: 16px;
}
.coffee-box .coffee-info .title button span {
  font-family: sans-serif;
  font-weight: 600;
}

.coffee-box .coffee-info ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 5px 0;
  margin-top: 8px;
}

.coffee-box .coffee-info ul li {
  user-select: none;
  font-weight: normal;
  background: var(--secondry-color);
  padding: 2px 15px;
  font-size: 15px;
  border-radius: 6px;
  color: var(--primery-color);
  margin-inline-end: 7px;
  margin-bottom: 3px;
  cursor: pointer;
  transition: 0.5s ease-out;
}
.coffee-box .coffee-info ul p {
  cursor: pointer;
  font-size: 11px;
  font-weight: 600;
  color: #5e5e5e;
  display: flex;
  align-items: center;
  margin-inline-start: 4px;
  color: var(--primery-color);
  text-decoration: underline;
  font-size: 14px;
}

.coffee-box .coffee-info ul .active {
  background: #7e4226;
  color: white;
}

.coffee-box .coffee-info .description {
  font-size: 12px;
  color: #5e5e5e;
}
.coffee-box .coffee-info .description span {
  cursor: pointer;
  margin-inline-start: 4px;
  color: var(--primery-color);
  text-decoration: underline;
}

.coffee-box .coffee-info .price {
  height: 100%;
  display: flex;
  align-items: end;
  justify-content: space-between;
  margin-top: 16px;
  font-size: 16px;
  font-weight: 600;
}
.coffee-box .coffee-info .price p {
  font-size: 14px;
  font-weight: 800;
  color: var(--primery-color);

}
.coffee-box .coffee-info .price span {
  font-family: sans-serif;
  font-weight: 800;
}

.coffee-box .coffee-info .price ul {
  all: unset;
  display: flex;
  list-style: none;
  background-color: var(--primery-color);
  border-radius: 15px;
}

.coffee-box .coffee-info .price ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  margin: 0;
  border-radius: 50%;
  background: white;
  color: var(--primery-color);
  font-weight: 900;
  border: 2px solid var(--primery-color);
  padding: 0;
  width: 25px;
  height: 25px;
  font-size: 20px;
}

.coffee-box .coffee-info .price ul li:nth-of-type(2) {
  border-radius: 0;
  color: white;
  background-color: var(--primery-color);
  font-size: 14px;
}

@media screen and (max-width: 600px) {
  .coffee-box .coffee-info ul li {
    font-size: 14px;
  }

}
@media screen and (max-width: 340px) {
  .coffee-box{
    width: 90%;
  }
}
