.search_content {
  background-color: white;
  width: 100%;
}
.search_content form {
  width: 100%;
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  padding: 32px 5%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 2;
}
.search_content form .icon {
  position: relative;
}
.search_content form .icon img {
  cursor: pointer;
  width: 35px;
  height: 35px;
  vertical-align: middle;
}
.search_content form .icon span {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: orange;
}
.search_content form .icon .dataFound {
  display: block;
}
@media screen and (max-width: 600px) {
  .search_content form .icon {
    margin-inline-end: 0;
  }
}
.search_content form .input_group {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  max-width: 600px;
}
.search_content form .input_group label img {
  cursor: pointer;
  position: absolute;
  border-radius: 0 4px 4px 0;
  height: calc(100% - 2px);
  padding: 4px;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--secondry-color);
}
.search_content form .input_group > input {
  width: 100%;
  padding: 8px;
  text-indent: 30px;
  outline: none;
  border-radius: 6px;
  border: none;
  font-size: 14px;
  background-color: var(--secondry-color);
  color: var(--primery-color);
  font-weight: 600;
}
.search_content form .input_group > input::-moz-placeholder {
  color: var(--primery-color);
}
.search_content form .input_group > input::placeholder {
  color: var(--primery-color);
}
.search_content form .input_group > input:focus {
  border-bottom: 1px solid var(--primery-color);
  border-radius: 6px 6px 0px 0px;
}
.search_content form .input_group ul {
  max-height: 310px;
  overflow-y: hidden;
  display: none;
  width: 100%;
  position: absolute;
  left: 0;
  top: 100%;
  background: var(--secondry-color);
  list-style: none;
}
.search_content form .input_group ul::-webkit-scrollbar {
  width: 5px;
  background-color: var(--secondry-color);
}
.search_content form .input_group ul::-webkit-scrollbar-thumb {
  background-color: var(--primery-color);
}
.search_content form .input_group ul::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  bottom: 0px;
  left: 0px;
  border-left: 3px solid var(--primery-color);
  border-bottom: 3px solid var(--primery-color);
}
.search_content form .input_group ul::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  bottom: 0px;
  right: 0px;
  border-right: 3px solid var(--primery-color);
  border-bottom: 3px solid var(--primery-color);
}
.search_content form .input_group ul li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--primery-color);
  padding: 8px 10px;
  font-size: 14px;
  font-weight: 600;
  color: var(--primery-color);
  transition: 0.3s;
  cursor: pointer;
}
.search_content form .input_group ul li input {
  all: unset;
  width: calc(100% - 30px);
}
.search_content form .input_group ul li:hover {
  background-color: var(--primery-color);
  color: var(--secondry-color);
}
.search_content form .input_group ul li:hover svg {
  stroke: var(--secondry-color);
}
.search_content form .input_group ul li svg {
  width: 15px;
  height: 15px;
  justify-self: end;
  stroke-width: 0.5;
  stroke: var(--primery-color);
}
.search_content form .back a {
  all: unset;
}
.search_content form .back a svg {
  cursor: pointer;
  width: 35px;
  height: 35px;
  vertical-align: middle;
  transform: rotateY(180deg);
}
.search_content .box-content {
  margin-top: 100px;
  padding-bottom: 20px;
  width: 95%;
  margin-inline: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  gap: 20px 10px;
}
.search_content .box-content::-webkit-scrollbar {
  width: 5px;
  background-color: var(--secondry-color);
}
.search_content .box-content::-webkit-scrollbar-thumb {
  background-color: var(--primery-color);
}
@media screen and (max-width: 1325px) {
  .search_content .box-content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 801px) {
  .search_content .box-content {
    padding-inline: 10px;
    grid-template-columns: repeat(1, 1fr);
  }
}
.search_content .cap_pic {
  width: 60vw;
  max-width: 300px;
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);
  z-index: -1;
}/*# sourceMappingURL=SearchList.css.map */