header {
  position: relative;
  width: 100%;
  height: 70px;
}
header .top_head {
  width: 100%;
  background-color: #fcfcfc;
  padding-inline: 5%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row-reverse;
  gap: 10px;
  z-index: 2;
}
header .top_head .logo img {
  height: 80px;
}
header .top_head .icon {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}
header .top_head .icon img {
  cursor: pointer;
  width: 35px;
  height: 35px;
  vertical-align: middle;
}
header .top_head .icon span {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: orange;
}
header .top_head .icon .dataFound {
  display: block;
}
@media screen and (max-width: 600px) {
  header .top_head .icon {
    margin-inline-end: 0;
  }
}/*# sourceMappingURL=Header.css.map */